import VueRunSfc from 'vue-run-sfc'

Vue.use(VueRunSfc, {
  // 全局配置(非必填),  具体属性含义参考 `props` 说明
  // 注意 全局的 和 局部的 `cssLabs` 和 `jsLabs` 是 merge的关系, 不是替换
  css: `
    .tree-ul {
      margin: 0;
      padding: 0 0 0 15px;
    }
    .tree-li {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      align-items: center;
    }
    .tree-checkbox {
      margin: 0 0 0 4px;
      transform: translateY(1px);
    }
    .tree-content {
      margin: 0 0 0 3px;
    }
    .tree-text {
      margin: 0;
    }
    .point {
      cursor: pointer;
    }
    .ldq-tree {
      user-select: none;
    }
  `,
  row: true,
  reverse: true,
  height: '400px',
  open: true,
  isHideHeader: false,
  themeColor: '#6190E8'
})
Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
